<template>
	<div class="job-div">
		<h1>本厂现应生产需要，招聘以下人员：</h1>
		<template>
		 <el-table
		    :data="tableData"
		    border
		    style="width: 100%">
		    <el-table-column
		      prop="title"
		      label="职业名称"
		      width="100">
		    </el-table-column>
		    <el-table-column
		      prop="number"
		      label="招聘人数"
		      width="100">
		    </el-table-column>
		    <el-table-column
		      prop="edu"
		      label="学历要求"
		      width="100">
		    </el-table-column>
		    <el-table-column
		      prop="where"
		      label="工作地区"
		      width="150">
		    </el-table-column>
		    <el-table-column
		      prop="when"
		      label="发布日期"
		      width="100">
		    </el-table-column>
		    <el-table-column
		      prop="des"
		      label="职位描述"
		      >
		    </el-table-column>
		  </el-table>
		</template>
	</div>
</template>
<script type="text/javascript">
export default {
data () {
return {
	tableData: [{
          title: '焊工',
          number: '2',
          edu: '不限',
          where:'杭州富阳',
          when:'2018-08-15',
          des:'焊工',
        }, 
    ]
};
},
};
</script>
<style  scoped>
.job-div{
	text-align: left;
}
.job-div h1{
	font-size: 20px;
	font-weight: 500;
	margin-bottom: 30px;
}
</style>


