<template>
  <div class="div-bg">
    <div class="div-wrap">
      <el-container>
        <el-aside width='230px'>
          <div class="item-bar">
              <el-card class="box-card">
                <div slot="header" class="clearfix">
                  <span class="clearfix-span">关于我们</span>
                </div>
                <div class="text item">
                  <span v-for='(item,index) in produtList' class="text-item-span">
                      <el-button  type="primary" plain @click='jump(index)'><i class="el-icon-caret-right"></i>{{item.title}}</el-button>
                      <br>
                      </span>
                </div>
              </el-card>
          </div>
        </el-aside>
        <el-container>
          <el-header>
            <div class="header-nav">          
              <el-breadcrumb separator="/">
                <el-breadcrumb-item :to="{ path: '/' }">首页</el-breadcrumb-item>
                <el-breadcrumb-item><a href="/">关于我们</a></el-breadcrumb-item>
              </el-breadcrumb>
            </div>
          </el-header>


          <el-main>
            <keep-alive>
              <router-view></router-view>
            </keep-alive>
          </el-main>
        </el-container>
      </el-container>
  </div>
</div>
</template>
<script type="text/javascript">


export default {

props:[],
data () {
return {
      produtList:[
  {
    title:'公司介绍',
    name:'aboutcom'
  },
  {
    title:'公司组织结构图',
    name:'zuzhi'
  },
  ]
};
},
methods:{
  jump(index){

      this.$router.push({name:this.produtList[index].name})

      },
}
};
</script>
<style  scoped>

.text-item-span{
  width: 100%;
  height: 40px;
  float: left;
}
.el-button{
 width: 150px;
}
</style>


