<template>
	<div class="contect-div">
    <div class="contect-div-header">
		<h1>杭州金燚成套设备有限公司</h1>
		<ul>
			<li v-for='(item,index) in contectway' :key='index'>
				<span class="contect-left">{{item.way}}:</span>
				<span class="contect-right">{{item.how}}</span>
			</li>
		</ul>
  </div>
		<div class="amap-page-container">
      	<el-amap vid="amapDemo" :zoom="zoom" :center="center" class="amap-demo" :plugin="plugin">
	        <el-amap-info-window  :position="mywindow.position" :content="mywindow.content" :visible="mywindow.visible" :events="mywindow.events"></el-amap-info-window>
	        <el-amap-marker :position="marker.position" :events="marker.events" :visible="marker.visible" :draggable="marker.draggable"></el-amap-marker>
	        <el-amap-circle :center="circle.center" :radius="circle.radius" :fillOpacity="circle.fillOpacity" :events="circle.events" :strokeColor="circle.strokeColor" :strokeStyle="circle.strokeStyle" :fillColor="circle.fillColor"></el-amap-circle>

      </el-amap>
    </div>
		
	</div>
</template>
<script type="text/javascript">
export default {
  data () {
    return {

    	contectway:[
      	{
      		way:'邮编',
      		how:'311400',
      	},
      	{
      		way:'电话',
      		how:'13867456735',
      	},
        {
          way:'座机',
          how:'0571-63336431',
        },
      	{
      		way:'网址',
      		how:'www.jinyi.com',
      	},
      	{
      		way:'邮箱',
      		how:'1318448313@qq.com',
      	},
      	{
      		way:'地址',
      		how:'浙江省杭州市富阳区华虹路138号',
      	},
      ],

      zoom: 15,
      center: [119.910386,30.051083],
      circle: {
        clickable: true,
        center: [119.910386,30.051083],
        radius: 200,
        fillOpacity: 0.3,
        strokeStyle: 'dashed',
        fillColor: '#FFFF00',
        strokeColor: '#00BFFF'
      },
      marker: {
        position: [119.910386,30.051083],
        events: {
          click: () => {
            if (this.mywindow.visible === true) {
              this.mywindow.visible = false
            } else {
              this.mywindow.visible = true
            }
          },
          dragend: (e) => {
            this.markers[0].position = [e.lnglat.lng, e.lnglat.lat]
          }
        },
        visible: true,
        draggable: false
      },
      mywindow: {
        position: [119.910386,30.051083],
        content: '<h4>杭州市金燚成套设备有限公司</h4><div class="text item"><i class="el-icon-location"></i>&nbsp&nbsp杭州市富阳区华虹路198号</div><div class="text item"><i class="el-icon-phone-outline"></i>&nbsp&nbsp0571-63336431 </div>',
        visible: true,
        events: {
          close () {
            this.mywindow.visible = false
          }
        }
      },
      plugin: {
        pName: 'Scale',
        events: {
          init (instance) {
            console.log(instance)
          }
        }
      }
    }
  }
}

</script>
<style  scoped>
  .amap-page-container {
    height: 450px;
    border:5px solid #fff;
  }
  .contect-div{
  text-align:left;	
  }
.contect-div-header{
  width:100%;
  height:240px;
}
 .contect-div-header h1{
	font-size:20px;
	font-weight:600;
 }
 .contect-div-header ul{
 	margin-top:30px;
 	margin-bottom:30px;
 }
 .contect-div-header li{
  width:100%;
  height:30px;
 	line-height:30px;
 	color:#777;
 }
 .contect-right{
 	margin-left:50px;
 }
</style>


