<template>
	<div>
		<div class="app-head">
			<div class="app-head-head">
				<h1>欢迎来到杭州金燚成套设备有限公司</h1>
				<h2>联系电话:13867456735</h2>
			</div>
			<div class="app-head-inner">
				<a href="javascript:;" class="app-head-logo"></a>
			</div>
			<div class="head-nav">
				<div class="nav-wrap">
				
								<el-menu :default-active="activeIndex" class="el-menu-demo" mode="horizontal" @select="handleSelect">
								  <el-menu-item index="1" @click='jump(0)'>{{bar[0].title}}</el-menu-item>
								  <el-menu-item index="2" @click='jump(1)'>{{bar[1].title}}</el-menu-item>
								  <el-submenu index="3" @click='jump(2)'>
								    <template slot="title">{{bar[2].title}}</template>
								    <el-menu-item index="2-1" @click='jumpto(0)'>{{bar[2].list[0].title}}</el-menu-item>
								    <el-menu-item index="2-2" @click='jumpto(1)'>{{bar[2].list[1].title}}</el-menu-item>
								    <el-menu-item index="2-3" @click='jumpto(2)'>{{bar[2].list[2].title}}</el-menu-item>
								    <el-menu-item index="2-4" @click='jumpto(3)'>{{bar[2].list[3].title}}</el-menu-item>
								  </el-submenu>
								  <el-menu-item index="4" @click='jump(3)'>{{bar[3].title}}</el-menu-item>
								  <el-menu-item index="5" @click='jump(4)'>{{bar[4].title}}</el-menu-item>
								  <el-menu-item index="6" @click='jump(5)'>{{bar[5].title}}</el-menu-item>
								  <el-menu-item index="7" @click='jump(6)'>{{bar[6].title}}</el-menu-item>
								</el-menu>
								<div class="line"></div>
					</div>
			</div>
		</div>
		<div class="app-content">
			<keep-alive>
				<router-view/>
			</keep-alive>
		</div>
		<div class="app-foot">
			<div class="app-foot-foot">
			<p>@ 2016 MIT00</p>
			</div>
		</div>
	</div>
</template>
<script type="text/javascript">
export default {
name: 'layout',
data () {
return {
	activeIndex: '1',
	toggleshow:false,
	bar:[ 
		 /*所有第一级菜单*/
		 { 
		  title:'首页', 
		  id:'/',        
		  name:'IndexPage',     
		 }, 
		 { 
		  title:'关于我们', 
		  id:'about', 
		  name:'aboutcom', 
		 }, 
		 { 
		  title:'产品展示', 
		  id:'show', 
		  name:'ProductionShow',
		  list:[
		  {
		  	title:'钣金加工',
		  	name:'banjinjiagong'
		  },
		  {
		  	title:'窨井盖',
		  	name:'yinjinggai'
		  },
		  {
		  	title:'市政设施',
		  	name:'shizhengsheshi'
		  },
		  {
		  	title:'控制设备',
		  	name:'kongzhishebei'
		  }

		  ]
		 }, 
		 {
		 	title:'仪器设备',
		 	id:'equipment',
		 	name:'Equipment',
		 },
		 {
		 	title:'新闻资讯',
		 	id:'news',
		 	name:'newsItem',
		 }, 
		  { 
		  title:'人才招聘', 
		  id:'jobs',  
		  name:'Jobs',
		 }, 
		  { 
		  title:'联系我们', 
		  id:'contect',  
		  name:'ContectUs',
		 }, 
]
};
},
methods:{
	jump(index){

	    this.$router.push({name:this.bar[index].name})

	    },
	jumpto(index){

	    this.$router.push({name:this.bar[2].list[index].name})

	    },  
	handleSelect(key, keyPath) {
        console.log(key, keyPath);
      }
}
};
</script>
<style>
	/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/

html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed, 
figure, figcaption, footer, header, hgroup, 
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
	margin: 0;
	padding: 0;
	border: 0;
	font-size: 100%;
	font: inherit;
	vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure, 
footer, header, hgroup, menu, nav, section {
	display: block;
}
body {
	line-height: 1;
}
ol, ul {
	list-style: none;
}
blockquote, q {
	quotes: none;
}
blockquote:before, blockquote:after,
q:before, q:after {
	content: '';
	content: none;
}
table {
	border-collapse: collapse;
	border-spacing: 0;
}
a{
	text-decoration: none;
}
.app-head {
    top: 0px;
    left: 0px;
    right: 0px;
    z-index: 2;
    height: 170px;
    background-color: #fff;
    color: #292E35;
    box-shadow: 0 0 2px 0 rgba(1, 1, 1, 0.2); 
    overflow: hidden;
    margin-bottom: 1px;
    min-width: 1200px;
}
.app-head-head{
	width: 100%;
	height: 20px;
	text-align: left;
	border-bottom: 1px solid #ddd;
	color: #aaa;
}
.app-head-head:hover{
	color: #111;
}
.app-head-head h1{
	font-size: 13px;
	line-height: 20px;
	margin-left: 90px;
	float: left;

}
.app-head-head h2{
	font-size: 13px;
	line-height: 20px;
	margin-right: 90px;
	float: right;

}
.app-head-inner{
	height: 100px;
	width: 1200px;
	position: relative;
	margin: 0 auto 0 auto;
}
.app-head-logo {
	width: 100%;
	height: 100px;
	background: url('../assets/images/logo1.jpg') left center no-repeat;
	background-size: 300px 46px;
	position: absolute;
	top: 0;
	left: 0;
}

.head-nav {
	height: 100px;
	width: 100%;
	background: #fff;
	text-align: center;

}
.nav-wrap{
	height: 70px;
	width: 700px;
	margin: 0 auto 0 auto;
	text-align: center;
	line-height: 50px;
}

.app-foot{
	text-align: center;
	min-width: 1200px;
	height: 95px;
}
.app-foot-foot{
	text-align: center;
	width: 100%;
	height: 90px;
	line-height: 90px;
	background: #e3e4e8;
	clear: both;
}
.app-content{
	width: 100%;
	margin: 0 auto;
}


/* //page的css */
 .el-button{
 	margin-top: 15px;
 }
 .el-aside {
    color: #333;
    height: 1000px;
    width: 200px;
    margin-right: 20px;
    overflow: hidden;
  }
  .el-main {

    color: #333;
    min-height: 900px;
  }
  .item-bar{
    width: 100%;
    height: 300px;
    overflow: hidden;
  }
  .header-nav{
  	border-bottom: 1px #111 solid;
  	padding-bottom: 10px;
  }

.el-breadcrumb{
  margin-top: 20px;
  margin-left: 20px;
}

 .text {
    font-size: 14px;
  }

  .item {
    margin-bottom: 18px;
  }
  .clearfix-span{
  	font-size: 20px;
  	font-weight: 600;
  }
  .clearfix:before,
  .clearfix:after {
    display: table;
    content: "";
  }
  .clearfix:after {
    clear: both
  }

  .box-card {
    width: 228px;
    height: 300px;
  }
  .div-bg{
  width: 100%;
  min-width: 1200px;
   text-align: center;
  background: url('../assets/background/background1.jpg')
}

 .div-wrap{
  width: 1200px;
  overflow: hidden;
  margin:0 auto;
 }




/*  //newsitem.css */
.news-div{
	text-align: center;
	width: 100%;
}
.news-div-header{
	border-bottom: 1px #555 dotted;
	width: 100%;
	height: 90px;
}
.news-div-header h1{
	font-size: 20px;
	font-weight: 600;
	margin-bottom: 20px;
}
.news-div-header h2{
	float: left;
	margin-right: 10px;
	font-size: 14px;
	color: #999;
}
.news-div-header-small{
	width: 350px;
	margin: 0 auto;
}
.news-div-body{
	width: 100%;
	border-bottom: 1px #555 dotted;
	padding-bottom: 20px;
}
.news-div-body p{
	text-align: left;
	text-indent: 2em;
	line-height: 20px;
	margin-top: 20px;
	color: #666;
}
.news-div-nav{
	text-align: left;
	margin: 20px 0;
}
.left{
	float: left;
}
.right{
	float: right;
}
.news-div-more{
	margin-top: 50px;
}
.news-div-more-header{
	width: 100%;
	height: 30px;
	border-bottom: 1px #555 dotted;
}
.news-div-more-header h1{
	float: left;
	font-size: 18px;
	font-weight: 600;
}
.news-div-more-body{
	margin-top: 20px;
	width: 100%;
}
.news-div-more-body li{
	width: 100%;
	height: 20px;
}


</style>


