<template>
	<div class="index-wrap">
		<div class="index-wrap-screen1">
			<slide-show :slide='slides' :inv='invTime'></slide-show>
	    </div>
	    <div class="index-wrap-screen2">
	    	<div class="hr">
	    		<h1>{{cmpInt.title}}</h1>
	    		<br>
	    		<h2>Company Profile</h2>
	    		<hr>
	    		<p class="word">{{cmpInt.word}}</p>
	    		<br><br>
	    		<div class="profileDiv">
	    		<p class="profile">{{cmpInt.profile}}</p>
	    		</div>
            	<el-button type='primary' class="button" @click='jump'>了解更多</el-button>

	    	</div>	
	    </div>

	    <div class="index-wrap-screen3">
			<div class="index-wrap-screen3-head">
			    <h1>PRODUCT</h1>
			    <hr>
			</div>
			<div class="screen3-product">
				<div class="screen3-product-nav">
					<div class="nav-one"><span>产品展示</span></div>
				</div>
				<div class="screen3-product-content">
					<product></product>
				</div>
			</div>

	    </div>
	    <div class="index-wrap-screen4">
	    	<div class="screen4-contect">
	    		<div class="contect-head">
	    			<h1>联系我们</h1>
	    			<img src="../assets/images/联系方式 图片.png" height="50" width="250">
	    		</div>
	    		<div class="contect-body">
	    		 <el-table
				    :data="contectway"
				    stripe
				    style="width: 100%">
				    <el-table-column
				      prop="way"
				      label="联系方式"
				      width="150">
				    </el-table-column>
				    <el-table-column
				      prop="how"
				      label="联系方法">
				    </el-table-column>
				  </el-table>

	    		</div>
	    	</div>
	    	<div class="screen4-news">
	    		<div class="news-head">   			
	    		</div>
	    		<div class="news-body">
	    			<h1>公司新闻</h1>
	    			<div style="width:100%; border:5px solid #409cee;"></div>
	    			<div class="news-item">
	    				<ul>
