	    						<router-link :to="{name:item.name}">
	    							<i class="el-icon-caret-right"></i>
	    							{{item.title}}
	    					</router-link>
	    						<p>[2018-08-18]</p>
	    					</li>
	    				</ul>
	    			</div>
	    		</div>
	    	</div>
	    </div>
  </div>

</div>
</template>

<script type="text/javascript">
import product from '../components/product.vue'
import slideShow from '../components/slideShow.vue'
export default {
name:'IndexPage',
components:{
	product,
	slideShow,
},
data () {
return {
      cmpInt:{
      	title:'公司简介',
      	word:'金燚求精,真诚合作',
      	profile:'杭州金燚成套设备有限公司成立于2018年4月，公司位于杭州富阳，是一家专业从事生产钣金加工、机柜成套、设备成套等高科技企业，主要产品有自动化控制柜、操作台、不锈钢机柜、井盖、教学仪器等。公司自成立以来，主要与浙江中控等一些高科技企业合作。公司有专业的机柜设计人员，专业的钣金加工人员，为保证产品的一流品质，增强企业的市场竞争力，公司配置了大型数控剪板机、数控折弯机、冲床、全自动卷板机等高精度设备与现代化办公设施，同时高薪聘请专业的设计人员，为客户提供优化的机柜加工、机柜成套方案。'
      },
      contectway:[
      	{
      		way:'地址',
      		how:'浙江省杭州市富阳区华虹路138号',
      	},
      	{
      		way:'邮编',
      		how:'311400',
      	},
      	{
      		way:'电话',
      		how:'13867456735',
      	},
      	{
          way:'座机',
          how:'0571-63336431',
        },
      	{
      		way:'邮箱',
      		how:'1318448313@qq.com',
      	}
      ],
      news:[
      	{
      		title:'浙江金燚成套公司网站上线！',
      		name:'newsOne'
      	},
      	{
      		title:'窨井盖安装说明',
      		name:'newsTwo'
      	},
      	{
      		title:'SMC片料简介',
      		name:'newsThree'
      	},
      	{
      		title:'浙江金燚成套公司坐落在富阳',
      		name:'newsFour'
      	},
      	{
      		title:'复合材料井盖产品介绍',
      		name:'newsSix'
      	}
      ]

     
        }
},
methods:{
	    jump(){
    //this.$router.push("/cart")
    //传递的参数用{{ $route.query.goodsId }}获取
    this.$router.push({name:'aboutcom'})
    //this.$router.go(-2)
    //后退两步
    },
}
}
</script>
<style  scoped>

.list-open-enter-active{
	transition:all .5s;
}
.list-open-enter{
	opacity:0.2;
	transform:scale(.5);
}
#left{
	float: left;
}
#right{
	float: right;
}
#right:hover{
	color: #red;
	text-shadow: 2px #000;
}
.index-wrap {

  width: 1200px;
  font-family: 微软雅黑;
  overflow: hidden;
  margin:0 auto;
}
.index-wrap-screen1 {
	width: 100%;
	margin: 0;
	padding: 0;
	margin-bottom: 20px;
}
.index-wrap-screen2{
	margin-top: 20px;
	width: 100%;
	margin: 0;
	padding: 0;
	text-align: center;
}
.hr {
	width:100%; 
  	margin-bottom: 20px;

}
.hr hr{
	width: 500px;

}
.hr h1{
	font-size: 25px;
	font-weight:bold;
}
.hr .word{
	color: #555;
}
.hr .profile{
	text-align: left;
	text-indent: 2em;
	line-height: 20px;
}
.profileDiv{
	width: 800px;
	margin: 0 auto;
}

.index-wrap-screen3{
	width: 100%;
	min-height: 800px;
	margin: 40px 0 20px 0;
	padding: 0;
	position: relative;
}
.index-wrap-screen3-head{
	width:100%;
	text-align:center;
}
.index-wrap-screen3-head h1{
	font-size:30px;
	font-weight:bold;
}
.screen3-product{
	width:100%;
	margin:0;
	padding:0;
}
.screen3-product-nav{
	width:100%;
	height:60px;
}
.screen3-product-content{
	width:100%;
	height:100%;
}
.nav-one{
	height:60px;
	width:150px; 
	margin-right:20px;
	background:#222;
	float:left;
	text-align:center;
	line-height:60px;
	color:#eee;
	font-size:20px;
	font-weight:700;
}

.screen4-contect{
	position: absolute;
	top: 0;
	left: 0;
	width: 350px;
	height: 500px;
}
.contect-head{
	width:100%;
	height:180px;
	margin-bottom:10px;
	text-align:center;
	border-radius:5px;
	background-color:#809fff;
	color:#eee;
}
.contect-head h1{
	font-size:50px;
	font-weight:500;
	line-height:100px;
}
.contect-body{
	border-style:dashed;
	padding-bottom :50px;
	width:100%;
}
.screen4-news{
	position:absolute;
	top:0;
	right:50px;
	height:550px;
	width:750px;
	margin-left:10px;
	overflow:hidden;
}
.news-head{
	width:100%;
	height:180px;
	background:url('../assets/news/newsbg2.jpg') center no-repeat;
	background-size:cover;
}
.news-item{
	width:600;
	height:100%;
	margin-top:15px;
}

.news-item p{
	float:right;
}

.news-body h1{
	font-size:25px;
	font-weight:600;
	line-height:50px;
	margin:10px 20px 10px 0;
	padding-left:20px;
	display:block;
	background:#222;
	width:120px;
	color:#fff;
}
.news-body li{
	line-height:40px;
}
.index-wrap-screen4{
	width: 100%;
	height: 550px;
	margin: 60px 0 20px 0;
	padding: 0;
	position: relative;
}

</style>


