              <p><span>杭州金燚成套设备有限公司</span>成立于2018年4月，公司位于杭州富阳，是一家专业从事生产钣金加工、机柜成套、设备成套等高科技企业，主要产品有自动化控制柜、操作台、不锈钢机柜、井盖、教学仪器等。公司自成立以来，主要与浙江中控等一些高科技企业合作。公司有专业的机柜设计人员，专业的钣金加工人员，为保证产品的一流品质，增强企业的市场竞争力，公司配置了大型数控剪板机、数控折弯机、冲床、全自动卷板机等高精度设备与现代化办公设施，同时高薪聘请专业的设计人员，为客户提供优化的机柜加工、机柜成套方案。</p>
