<template>
	<div>
			  <el-row>
                <el-col :span="12" v-for="(item,index) in newsList" :key='index' >
                  <el-card :body-style="{ padding: '0px' }">
                    <img :src='item.src'  class="image">
                    <div style="padding: 14px;">
                      <span>{{item.title}}</span>
                      <div class="bottom clearfix">
                        <time class="time">{{ currentDate }}</time>
                        <el-button type="text" class="button" @click='jump(index)'>点击查看</el-button>
                      </div>
                    </div>
                  </el-card>
                </el-col>
              </el-row>
	</div>
</template>
<script type="text/javascript">
export default {

data () {
return {
	newsList:[
		{
			title:'杭州金燚成套设备有限公司网站正式上线！',
			src:require('../../assets/news/news1.jpg'),
			name:'newsOne'
		},
		{
			title:'窨井盖安装说明',
			src:require('../../assets/news/news2.jpg'),
			name:'newsTwo'
		},
		{
			title:'SMC片料简介',
			src:require('../../assets/news/news3.jpg'),
			name:'newsThree'
		},
		{
			title:'浙江金燚成套公司坐落在富阳',
			src:require('../../assets/news/news4.jpg'),
			name:'newsFour'
		},
		{
			title:'公司招聘新员工',
			src:require('../../assets/news/news5.jpg'),
			name:'newsFive'
		},
		{
			title:'复合材料井盖产品介绍',
			src:require('../../assets/news/news6.jpg'),
			name:'newsSix'
		},


	]
};
},
methods:{
	jump(index){

	    this.$router.push({name:this.newsList[index].name})

	    },
}
};
</script>
<style  scoped>
.el-card img{
	height: 300px;
}
.el-card{
  	width: 400px;
  	margin-top: 20px;
}
</style>


