<template>
 <div class="div-bg">
    <div class="div-wrap">
      <el-container>
        <el-aside width='230px'>
          <div class="item-bar">
              <el-card class="box-card">
                <div slot="header" class="clearfix">
                  <span class="clearfix-span">仪器设备</span>
                </div>
                <div class="text item">
                  <span v-for='(item,index) in produtList' class="text-item-span">
                      <el-button  type="primary" plain @click='jump(index)'><i class="el-icon-caret-right"></i>{{item.title}}</el-button>
                      <br>
                  </span>
                </div>
              </el-card>
          </div>
        </el-aside>
        <el-container>
          <el-header>
            <div class="header-nav">          
              <el-breadcrumb separator="/">
                <el-breadcrumb-item :to="{ path: '/' }">首页</el-breadcrumb-item>
                <el-breadcrumb-item><a href="/">仪器设备</a></el-breadcrumb-item>
              </el-breadcrumb>
            </div>
          </el-header>
          <el-main>
            <template>
              <el-carousel :interval="4000" type="card" height="200px">
                <el-carousel-item v-for="item in 3" :key="item">
                  <h3>暂无照片</h3>
                </el-carousel-item>
              </el-carousel>
            </template>
          </el-main>
        </el-container>
      </el-container>
  </div>
</div>
</template>
<script type="text/javascript">


export default {

props:[],
data () {
return {
   produtList:[
  {
    title:'仪器设备',
    name:'Equipment'
  },
  ]
};
},
methods:{
  jump(index){

      this.$router.push({name:this.produtList[index].name})

      },
}
};
</script>
<style  scoped>
.text-item-span{
  width: 100%;
  height: 40px;
  float: left;
}
.el-button{
 width: 150px;
}
.el-carousel__item h3 {
    color: #475669;
    font-size: 14px;
    opacity: 0.75;
    line-height: 200px;
    margin: 0;
  }
  
  .el-carousel__item:nth-child(2n) {
    background-color: #99a9bf;
  }
  
  .el-carousel__item:nth-child(2n+1) {
    background-color: #d3dce6;
  }
</style>


