import Vue from 'vue'
import Router from 'vue-router'
import Layout from '../components/layout.vue'
import IndexPage from '../page/index.vue'
import About from '../page/AboutUs.vue'
// import Service from '../page/Service.vue'
import Jobs from '../page/Jobs.vue'
import Contect from '../page/ContectUs.vue'
import News from '../page/News.vue'
import Equipment from '../page/Equipment.vue'
//产品分类
import Production from '../page/Production.vue'
import page1 from '../page/productClass/yjg.vue'
import page2 from '../page/productClass/bjjg.vue'
import page3 from '../page/productClass/szss.vue'
import page4 from '../page/productClass/kzsb.vue'
//窨井盖产品详情
import yjgItemOne from '../page/productClass/productItem/yinjinggai/yjgitemOne.vue'
import yjgItemTwo from '../page/productClass/productItem/yinjinggai/yjgitemTwo.vue'
import yjgItemThree from '../page/productClass/productItem/yinjinggai/yjgItemThree.vue'
import yjgItemFour from '../page/productClass/productItem/yinjinggai/yjgitemFour.vue'
import yjgItemFive from '../page/productClass/productItem/yinjinggai/yjgitemFive.vue'
import yjgItemSix from '../page/productClass/productItem/yinjinggai/yjgitemSix.vue'
import yjgItemSeven from '../page/productClass/productItem/yinjinggai/yjgitemSeven.vue'
import yjgItemEight from '../page/productClass/productItem/yinjinggai/yjgitemEight.vue'
import yjgItemNine from '../page/productClass/productItem/yinjinggai/yjgitemNine.vue'
import yjgItemTen from '../page/productClass/productItem/yinjinggai/yjgitemTen.vue'
import yjgItemEleven from '../page/productClass/productItem/yinjinggai/yjgitemEleven.vue'
import yjgItemTwelve from '../page/productClass/productItem/yinjinggai/yjgitemTwelve.vue'
//钣金加工产品详情
import bjjgItemOne from '../page/productClass/productItem/banjinjiagong/bjjgitemOne.vue'
import bjjgItemTwo from '../page/productClass/productItem/banjinjiagong/bjjgitemTwo.vue'
import bjjgItemThree from '../page/productClass/productItem/banjinjiagong/bjjgitemThree.vue'
import bjjgItemFour from '../page/productClass/productItem/banjinjiagong/bjjgitemFour.vue'
import bjjgItemFive from '../page/productClass/productItem/banjinjiagong/bjjgitemFive.vue'
//市政设施产品详情
import szssItemOne from '../page/productClass/productItem/shizhengsheshi/szssitemOne.vue'
import szssItemTwo from '../page/productClass/productItem/shizhengsheshi/szssitemTwo.vue'
//新闻
import newsItem from '../page/news/newsItem.vue'
import news1 from '../page/news/newsOne.vue'
import news2 from '../page/news/newsTwo.vue'
import news3 from '../page/news/newsThree.vue'
import news4 from '../page/news/newsFour.vue'
import news5 from '../page/news/newsFive.vue'
import news6 from '../page/news/newsSix.vue'
//关于公司
import aboutcom from '../page/about/aboutCom.vue'
import zuzhijiegou from '../page/about/zuzhijiegou.vue'
Vue.use(Router)

export default new Router({
    //mode:'history',
    //base:'/',
    mode:'hash',
    linkExactActiveClass:'active',
  routes: [
    {
      path: '/',
      name: 'IndexPage',
      component: IndexPage,
    },
    {
    	path:'/#/about',
    	name:'AboutUs',
    	component:About,
        children:[
            {
                path:'aboutcom',
                name:'aboutcom',
                component:aboutcom,
            },
            {
                path:'zuzhi',
                name:'zuzhi',
                component:zuzhijiegou,
            }
        ]
    },

    // {
    // 	path:'/service',
    // 	name:'Service',
    // 	component:Service,
    // },
    {
    	path:'/#/jobs',
    	name:'Jobs',
    	component:Jobs,
    },
    {
    	path:'/#/contect',
    	name:'ContectUs',
    	component:Contect,
    },
    {
        path:'/#/news',
        name:'News',
        component:News,
        children:[
            {
                path:'newitem',
                name:'newsItem',
                component:newsItem,
            },
            {
                path:'news1',
                name:'newsOne',
                component:news1,
            },
            {
                path:'news2',
                name:'newsTwo',
                component:news2,
            },
            {
                path:'news3',
                name:'newsThree',
                component:news3,
            },
            {
                path:'news4',
                name:'newsFour',
                component:news4,
            },
            {
                path:'news5',
                name:'newsFive',
                component:news5,
            },
            {
                path:'news6',
                name:'newsSix',
                component:news6,
            },
        ]
    },
    {
        path:'/#/equipment',
        name:'Equipment',
        component:Equipment,
    },
    {
            path: '/#/product',
            component: Production,
            redirect: '/product/yjg',
            children: [
                {
                    path: 'yjg',
                    name: 'yinjinggai',
                    component: page1
                },
                {
                    path: 'bjjg',
                    name:'banjinjiagong',
                    component: page2
                },
                {
                    path: 'szss',
                    name:'shizhengsheshi',
                    component: page3
                },
                {
                    path: 'kzsb',
                    name:'kongzhishebei',
                    component: page4
                },
                {
                    path:'yjgitem1',
                    name:'yjgItemOne',
                    component: yjgItemOne
                },
                {
                    path:'yjgitem2',
                    name:'yjgItemTwo',
                    component: yjgItemTwo
                },
                {
                    path:'yjgitem3',
                    name:'yjgItemThree',
                    component: yjgItemThree
                },
                {
                    path:'yjgitem4',
                    name:'yjgItemFour',
                    component: yjgItemFour
                },
                {
                    path:'yjgitem5',
                    name:'yjgItemFive',
                    component: yjgItemFive
                },
                {
                    path:'yjgitem6',
                    name:'yjgItemSix',
                    component: yjgItemSix
                },
                {
                    path:'yjgitem7',
                    name:'yjgItemSeven',
                    component: yjgItemSeven
                },
                {
                    path:'yjgitem8',
                    name:'yjgItemEight',
                    component: yjgItemEight
                },
                {
                    path:'yjgitem9',
                    name:'yjgItemNine',
                    component: yjgItemNine
                },
                {
                    path:'yjgitem10',
                    name:'yjgItemTen',
                    component: yjgItemTen
                },
                {
                    path:'yjgitem11',
                    name:'yjgItemEleven',
                    component: yjgItemEleven
                },
                {
                    path:'yjgitem12',
                    name:'yjgItemTwelve',
                    component: yjgItemTwelve
                },
                {
                    path:'bjjgitem1',
                    name:'bjjgItemOne',
                    component:bjjgItemOne
                },
                {
                    path:'bjjgitem2',
                    name:'bjjgItemTwo',
                    component:bjjgItemTwo
                },
                {
                    path:'bjjgitem3',
                    name:'bjjgItemThree',
                    component:bjjgItemThree
                },
                {
                    path:'bjjgitem4',
                    name:'bjjgItemFour',
                    component:bjjgItemFour
                },
                {
                    path:'bjjgitem5',
                    name:'bjjgItemFive',
                    component:bjjgItemFive
                },
                {
                    path:'szssitem1',
                    name:'szssItemOne',
                    component:szssItemOne
                },
                {
                    path:'szssitem2',
                    name:'szssItemTwo',
                    component:szssItemTwo
                },
            ]
        },
  ]
})



