<template>
	  <div class="block">
	    <el-carousel height="500px">
	      <el-carousel-item v-for="(item,index) in slides" :key="index">
	      	<router-link :to="{name:item.name}"><img :src="item.src" ></router-link>
	      </el-carousel-item>
	    </el-carousel>
	  </div>
</template>
<script type="text/javascript">
export default {
name: 'component_name',
props:[],
data () {
return {
	slides: [
        {
          src: require('../assets/slideShow/pic1.jpg'),
          title: 'xxx1',
          name:'banjinjiagong'
        },
        {
          src: require('../assets/slideShow/pic2.jpg'),
          title: 'xxx2',
          name:'yinjinggai'
        },
        {
          src: require('../assets/slideShow/pic3.jpg'),
          name:'shizhengsheshi'
        },
      ],
};
},
methods:{
  jump(index){

      this.$router.push({name:this.produtList[index].name})

      },
}
};
</script>
<style  scoped>
